import { combineReducers } from 'redux'
import { Types } from '../actionTypes'

const carrierReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_CARRIER_INFO:
      return payload

    default:
      return state
  }
}
const errorReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.CARRIER_ERROR:
      return payload

    default:
      return state
  }
}
const loaderReducer = (state = false, { type, payload }) => {
  switch (type) {
    case Types.START_CARRIER_LOADER:
      return (state = true)

    case Types.GET_CARRIER_INFO:
    case Types.CARRIER_ERROR:
      return (state = false)

    default:
      return state
  }
}

export const carrier = combineReducers({
  carrier: carrierReducer,
  error: errorReducer,
  loader: loaderReducer,
})
