import React from 'react'
import { useDispatch } from 'react-redux'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'
import { device } from '../stylesheets/device'
import { ReactComponent as CloseIcon } from '../images/close.svg'
import { ReactComponent as SearchIcon } from '../images/search.svg'
import { ReactComponent as ScheduleIcon } from '../images/schedule.svg'
import { ReactComponent as TripsIcon } from '../images/trips.svg'
import { ReactComponent as BusesIcon } from '../images/buses.svg'
import { ReactComponent as StuffIcon } from '../images/staff.svg'
import { ReactComponent as ReportsIcon } from '../images/reports.svg'
import { ReactComponent as CarrierIcon } from '../images/carrier.svg'
import { ReactComponent as ExitIcon } from '../images/exit.svg'

import LanguageSelect from './LanguageSelect'
import { sendTitlePage, setAuth } from '../redux/global/globalAction'
import { allClear } from '../redux/trips/tripsActions'

const width = window.innerWidth

const Navigation = ({ onClose = () => {} }) => {
  const dispatch = useDispatch()
  const handleLinkClick = (e, name) => {
    onClose()
    dispatch(sendTitlePage(name))
    dispatch(allClear())
  }

  const handleExit = () => {
    dispatch(setAuth(null))
    dispatch(sendTitlePage('schedule'))
  }
  return (
    <Container>
      <Header>
        {width >= 992 ? (
          <LanguageSelect />
        ) : (
          <>
            <Title>Меню</Title>
            <CloseIcon onClick={onClose} />
          </>
        )}
      </Header>
      <Nav>
        <StyledNavLink to="/orders" onClick={e => handleLinkClick(e, 'orders')}>
          <LinkTitle>Заказы</LinkTitle>
          <IconBox>
            <SearchIcon width={width >= 992 ? 24 : 74} height={width >= 992 ? 24 : 74} />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/schedule" onClick={e => handleLinkClick(e, 'schedule')}>
          <LinkTitle>Расписание</LinkTitle>
          <IconBox>
            <ScheduleIcon
              width={width >= 992 ? 24 : 76}
              height={width >= 992 ? 24 : 84}
            />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/trips" onClick={e => handleLinkClick(e, 'trips')}>
          <LinkTitle>Рейсы</LinkTitle>
          <IconBox>
            <TripsIcon width={width >= 992 ? 24 : 72} height={width >= 992 ? 24 : 60} />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/vehicles" onClick={e => handleLinkClick(e, 'vehicles')}>
          <LinkTitle>Автобусы</LinkTitle>
          <IconBox>
            <BusesIcon width={width >= 992 ? 24 : 68} height={width >= 992 ? 24 : 80} />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/staff" onClick={e => handleLinkClick(e, 'staff')}>
          <LinkTitle>Сотрудники</LinkTitle>
          <IconBox>
            <StuffIcon width={width >= 992 ? 24 : 92} height={width >= 992 ? 24 : 60} />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/reports" onClick={e => handleLinkClick(e, 'reports')}>
          <LinkTitle>Отчеты</LinkTitle>
          <IconBox>
            <ReportsIcon width={width >= 992 ? 24 : 76} height={width >= 992 ? 24 : 76} />
          </IconBox>
        </StyledNavLink>
        <StyledNavLink to="/carrier" onClick={e => handleLinkClick(e, 'carrier')}>
          <LinkTitle>Перевозчик</LinkTitle>
          <IconBox>
            <CarrierIcon width={width >= 992 ? 24 : 84} height={width >= 992 ? 24 : 76} />
          </IconBox>
        </StyledNavLink>
        <ExitButton onClick={handleExit}>
          <LinkTitle>Выйти</LinkTitle>
          <IconBox>
            <ExitIcon width={width >= 992 ? 24 : 84} height={width >= 992 ? 24 : 76} />
          </IconBox>
        </ExitButton>
      </Nav>
    </Container>
  )
}

export default Navigation

const Container = styled.div`
  width: 100vw;

  @media ${device.laptop} {
    border-right: 1px solid ${prop => prop.theme.mainColor};
    width: 22%;
  } ;
`
const Header = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 12.4px 24px;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
  @media ${device.laptop} {
    padding: 7px;
  }
`
const Title = styled.p`
  color: ${prop => prop.theme.textColorDark};
  font-size: 24px;
`
const Nav = styled.nav`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 12px;
  background-color: #fff;
  @media ${device.laptop} {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
  }
`
const StyledNavLink = styled(NavLink)`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 12px;
  &.active {
    @media ${device.laptop} {
      background-color: #feffcc;
    }
  }
  &.active > div {
    background-color: #feffcc;
    @media ${device.laptop} {
      background-color: transparent;
    }
  }
  &.active > p {
    font-weight: 700;
  }
  @media ${device.laptop} {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 0;
    width: 100%;
    padding: 12px;
  }
`
const LinkTitle = styled.p`
  font-size: 18px;
  color: ${prop => prop.theme.textColorDark};
  margin-bottom: 4px;
  @media ${device.laptop} {
    order: 1;
    margin: 0;
  }
`
const IconBox = styled.div`
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f2f2f2;
  border: 1px solid #a6a6a6;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  @media ${device.laptop} {
    width: auto;
    height: auto;
    background-color: transparent;
    border: none;
    box-shadow: none;
    margin-right: 12px;
  }
`
const ExitButton = styled.button`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 12px;
  background: none;
  border: none;
  @media ${device.laptop} {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 0;
    width: 100%;
    padding: 12px;
    border-top: 2px solid ${props => props.theme.mainColor};
  }
`
