import { Types } from '../actionTypes'

export const allTrips = data => ({ type: Types.GET_ALL_TRIPS, payload: data })

export const tripId = data => ({ type: Types.GET_TRIP_ID, payload: data })

export const removeTripId = id => ({ type: Types.REMOVE_TRIP, payload: id })

export const statusTrip = (id, status) => ({
  type: Types.STATUS_TRIP,
  payload: { id, status },
})
export const creteBackTrip = data => ({
  type: Types.CREATE_BACK_TRIP,
  payload: data,
})

export const tripPeriod = data => ({ type: Types.GET_TRIP_PERIOD, payload: data })

export const tripRoute = data => ({ type: Types.GET_TRIP_ROUTE, payload: data })

export const addNewStop = data => ({ type: Types.GET_ROUTE_STOPS, payload: data })

export const tripTariff = data => ({ type: Types.GET_TRIP_TARIFF, payload: data })

export const changeTariffValue = obj => ({ type: Types.CHANGE_TRIP_TARIFF, payload: obj })

export const setCurrentStop = value => ({ type: Types.GET_CURRENT_STOP, payload: value })

export const typeEdidTrip = val => ({ type: Types.TYPE_EDIT_TRIP, payload: val })

export const tripsLoader = () => ({ type: Types.START_TRIPS_LOADER })

export const tripsError = error => ({ type: Types.TRIPS_ERROR, payload: error })

export const allClear = () => ({ type: Types.CLEAR })
