import styled from 'styled-components'
import { device } from './device'

export const TextInputDiv = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
  & input {
    width: 100%;
    border: 1px solid
      ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorDark)};
    border-radius: 4px;
    padding: 6px 12px;
    &:focus,
    &:hover {
      outline: none;
      box-shadow: 0 0 2px 1px
        ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorLight)};
    }
  }
  & select {
    width: 100%;
    border: 1px solid
      ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorDark)};
    border-radius: 4px;
    padding: 6px 12px;
    &:focus,
    &:hover {
      outline: none;
      box-shadow: 0 0 2px 1px
        ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorLight)};
    }
  }
  & label {
    margin-bottom: 6px;
  }
  & p {
    color: red;
  }
`
export const Button = styled.button`
  min-width: ${props => (props.width ? props.width : null)};
  display: block;
  padding: 6px 20px;
  background-color: ${prop => prop.theme.mainColor};
  border: 1px solid ${prop => prop.theme.mainColor};
  border-radius: 4px;
  color: ${prop => prop.theme.textColorLight};
  cursor: pointer;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-size: 18px;
  transition: background-color 200ms linear;

  &:hover {
    background-color: #e2e600;
    transition: all 200ms linear;
  }
`
export const AnimeBox = styled.div`
  background-color: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  @media ${device.tablet} {
    padding: 52px 0;
  }
`
export const ButtonRound = styled.button`
  position: fixed;
  bottom: 16px;
  right: 16px;
  width: 70px;
  height: 70px;
  border: none;
  border-radius: 50%;
  background-color: ${prop => prop.theme.mainColor};
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  text-align: center;
  color: ${prop => prop.theme.textColorLight};
  font-size: 36px;
`
export const ErrorText = styled.p`
  color: ${props => props.theme.warningColor};
`
export const LoaderBox = styled.div`
  width: 200px;
  margin: 0 auto;
  padding-top: 100px;
`
