import { combineReducers } from 'redux'
import { Types } from '../actionTypes'

const reportsReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_REPORTS:
      return payload

    default:
      return state
  }
}
const loaderReducer = (state = false, { type, payload }) => {
  switch (type) {
    case Types.REPORTS_LOADER_START:
      return true

    case Types.GET_REPORTS:
    case Types.REPORTS_ERROR:
      return false

    default:
      return state
  }
}
const errorReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.REPORTS_ERROR:
      return payload

    default:
      return state
  }
}

export const reports = combineReducers({
  reports: reportsReducer,
  loader: loaderReducer,
  error: errorReducer,
})
