import { combineReducers } from 'redux'
import { Types } from '../actionTypes'

const pageTitleReduser = (state = 'schedule', { type, payload }) => {
  switch (type) {
    case Types.TITLE_PAGE:
      return payload

    default:
      return state
  }
}

const languageReducer = (state = { value: 'UA', label: 'UA' }, { type, payload }) => {
  switch (type) {
    case Types.CHANGE_LANGUAGE:
      return payload

    default:
      return state
  }
}

const authReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.AUTH:
      return payload

    default:
      return state
  }
}

export const global = combineReducers({
  pageTitle: pageTitleReduser,
  language: languageReducer,
  auth: authReducer,
})
