import { createStore, combineReducers, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import { persistStore, persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import { composeWithDevTools } from 'redux-devtools-extension'
import { global } from './global/globalReducers'
import { carrier } from './carrier/carrierReducer'
import { vehicles } from './vehicles/vehiclesReducers'
import { staff } from './staff/staffReducers'
import { trips } from './trips/tripsRedusers'
import { schedule } from './schedule/scheduleReducers'
import { orders } from './orders/ordersReducers'
import { reports } from './reports/reportsReducers'

const globalPersistConfig = {
  key: 'global',
  storage,
  whitelist: ['language', 'pageTitle', 'auth'],
}
const tripsPersistConfig = {
  key: 'trips',
  storage,
  whitelist: ['trip', 'route', 'typeTrip'],
}
const schedulePersistConfig = {
  key: 'schedule',
  storage,
  whitelist: ['schedule', 'currentTrip'],
}

const reportsPersistConfig = {
  key: 'report',
  storage,
  whitelist: ['reports'],
}

const ordersPersistConfig = {
  key: 'orders',
  storage,
  whitelist: ['orders'],
}
const rootReducer = combineReducers({
  global: persistReducer(globalPersistConfig, global),
  carrier: carrier,
  vehicles: vehicles,
  staff: staff,
  trips: persistReducer(tripsPersistConfig, trips),
  schedule: persistReducer(schedulePersistConfig, schedule),
  orders: persistReducer(ordersPersistConfig, orders),
  reports: persistReducer(reportsPersistConfig, reports),
})
const enhancer = applyMiddleware(thunk)

// export const store = createStore(persistedReducer, enhancer) //production

export const store = createStore(rootReducer, composeWithDevTools(enhancer))
export const persistor = persistStore(store)
