import { Types } from '../actionTypes'
import { combineReducers } from 'redux'

const ordersReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_ORDERS:
      return payload

    default:
      return state
  }
}
const loaderReducer = (state = false, { type }) => {
  switch (type) {
    case Types.ORDERS_LOADER_START:
      return true

    case Types.GET_ORDERS:
    case Types.ORDERS_ERROR:
      return false

    default:
      return state
  }
}
const errorReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.ORDERS_ERROR:
      return payload

    default:
      return state
  }
}
export const orders = combineReducers({
  orders: ordersReducer,
  loader: loaderReducer,
  error: errorReducer,
})
