import { Types } from '../actionTypes'
import { combineReducers } from 'redux'

const scheduleReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_SCHEDULE:
    case Types.DELETE_BUS:
    case Types.INCLUDE_TRIP:
    case Types.EXCLUDE_TRIP:
      return payload

    default:
      return state
  }
}

const crewReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_CREW:
      return payload

    case Types.ADD_MEMBER:
      return [...state, payload]

    default:
      return state
  }
}

const busesReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_BUSES:
      return payload

    default:
      return state
  }
}

const passengersReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_PASSENGERS:
      return payload

    default:
      return state
  }
}

const loaderReducer = (state = false, { type, payload }) => {
  switch (type) {
    case Types.SCHEDULE_LOADER_START:
      return true

    case Types.GET_SCHEDULE:
      return false

    default:
      return state
  }
}
const errorRedicer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.SCHEDULE_ERROR:
      return payload

    default:
      return state
  }
}

export const schedule = combineReducers({
  schedule: scheduleReducer,
  crew: crewReducer,
  buses: busesReducer,
  passengers: passengersReducer,
  loader: loaderReducer,
  error: errorRedicer,
})
