import React, { useState, useRef } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { getPageTitle } from '../redux/global/globalSelectors'
import { ReactComponent as MenuIcon } from '../images/menu.svg'
import LanguageSelect from './LanguageSelect'
import { CSSTransition } from 'react-transition-group'
import Navigation from './Navigation'
import { device } from '../stylesheets/device'

const width = window.innerWidth

const Header = () => {
  const [isShowMenu, setIsShowMenu] = useState(false)
  const toggleShowMenu = () => setIsShowMenu(!isShowMenu)
  const backdropRef = useRef(null)
  const titlePage = useSelector(getPageTitle)
  return (
    <>
      {width >= 992 ? (
        <HeaderBox>
          <Title>{titlePage}</Title>
        </HeaderBox>
      ) : (
        <>
          <HeaderBox>
            <MenuIcon
              style={{ color: '#fff' }}
              fontSize="large"
              onClick={toggleShowMenu}
            />
            <Title>{titlePage}</Title>
            <LanguageSelect />
          </HeaderBox>
          <CSSTransition
            in={isShowMenu}
            timeout={300}
            classNames="menu"
            unmountOnExit
            nodeRef={backdropRef}
          >
            <NavigationBox ref={backdropRef}>
              <Navigation onClose={toggleShowMenu} />
            </NavigationBox>
          </CSSTransition>
        </>
      )}
    </>
  )
}

export default Header

const HeaderBox = styled.header`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px 24px;
  background-color: ${prop => prop.theme.mainColor};

  @media ${device.laptop} {
    padding: 12.4px 12px;
    min-height: 52px;
  }
`
const NavigationBox = styled.div`
  position: fixed;
  background-color: #fff;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  z-index: 1;
`

const Title = styled.p`
  font-size: 24px;
  margin-left: 12px;
`
