import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import styled from 'styled-components'
import { Formik, Form, useField } from 'formik'
import * as Yup from 'yup'
import { Button } from '../stylesheets/mainStyled'
import { useHistory } from 'react-router-dom'
import { authorization } from '../services/apiMethods'
import { setAuth } from '../redux/global/globalAction'
import Loading from '../components/Loader'

const Authorization = () => {
  const [error, setError] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useDispatch()
  const history = useHistory()
  return (
    <Container>
      <Title>Приветствуем!</Title>
      <SubTitle>Для начала работы введите логин и пароль</SubTitle>
      <Formik
        initialValues={{
          login: 'carrier_test',
          password: 'test_carrier',
        }}
        validationSchema={Yup.object({
          login: Yup.string().required('is required'),
          password: Yup.string().required('is required'),
        })}
        onSubmit={async ({ login, password }) => {
          const key = window.btoa(`${login}:${password}`)
          setIsLoading(true)
          try {
            const response = await authorization(key)
            if (response.status === 200) {
              dispatch(setAuth(response.config.headers.Authorization))
              setIsLoading(false)
              history.replace('/schedule')
            } else {
              setIsLoading(false)
              setError('Не верный логин или пароль')
            }
          } catch (error) {
            setError('Не верный логин или пароль')
            setIsLoading(false)
          }
        }}
      >
        <Form>
          <FormBox>
            <MyTextInput label="Логин" name="login" type="text" />
            <MyTextInput label="Пароль" name="password" type="password" />
            <Button type="submit">
              {isLoading ? (
                <Loading type="ThreeDots" color="#00BFFF" height={15} width={50.38} />
              ) : (
                'Войти'
              )}
            </Button>
            {error && <ErrorText>{error}</ErrorText>}
          </FormBox>
        </Form>
      </Formik>
    </Container>
  )
}

export default Authorization

const Container = styled.div`
  padding: 198px 10px 0;
`

const MyTextInput = ({ label, ...props }) => {
  const [field, meta] = useField(props)
  return (
    <TextInputDiv error={meta.touched && meta.error}>
      <label htmlFor={props.id || props.name}>{label}</label>
      <div>
        <input {...field} {...props} />
        {meta.touched && meta.error ? <p>{meta.error}</p> : null}
      </div>
    </TextInputDiv>
  )
}
const Title = styled.h2`
  margin: 0 auto 88px;
  text-align: center;
  font-weight: 700;
`
const FormBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 300px;
  margin: 0 auto;
`

const SubTitle = styled.h2`
  font-weight: 400;
  margin-bottom: 28px;
  text-align: center;
`
export const TextInputDiv = styled.div`
  display: flex;
  margin-bottom: 12px;
  font-size: 18px;
  & input {
    border: 1px solid
      ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorDark)};
    border-radius: 4px;
    padding: 6px 12px;
    &:focus,
    &:hover {
      outline: none;
      box-shadow: 0 0 2px 1px
        ${prop => (prop.error ? prop.theme.warningColor : prop.theme.textColorLight)};
    }
  }
  & label {
    margin-right: 12px;
    padding-top: 6px;
    font-size: 24px;
  }
  & p {
    color: ${prop => prop.theme.warningColor};
  }
`
const ErrorText = styled.p`
  color: ${prop => prop.theme.warningColor};
  padding-top: 10px;
`
