import { Types } from '../actionTypes'
import { combineReducers } from 'redux'

const staffReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_STAFF:
      return payload

    case Types.EDIT_MEMBER:
      return [...state.filter(el => el.id !== payload.id), payload]

    case Types.ADD_MEMBER:
      return [...state, payload]

    case Types.REMOVE_MEMBER:
      return state.filter(el => el.id !== payload)

    default:
      return state
  }
}
const memberReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_MEMBER:
    case Types.EDIT_MEMBER:
      return payload

    default:
      return state
  }
}

const loaderReducer = (state = false, { type }) => {
  switch (type) {
    case Types.START_STAFF_LOADER:
      return true

    case Types.STAFF_ERROR:
    case Types.GET_STAFF:
    case Types.GET_MEMBER:
    case Types.EDIT_MEMBER:
    case Types.ADD_MEMBER:
      return false

    default:
      return state
  }
}

const errorReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.STAFF_ERROR:
      return payload

    default:
      return state
  }
}

export const staff = combineReducers({
  staff: staffReducer,
  member: memberReducer,
  loader: loaderReducer,
  error: errorReducer,
})
