import { Types } from '../actionTypes'
import { combineReducers } from 'redux'

const tripsReduser = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_ALL_TRIPS:
      return payload

    case Types.REMOVE_TRIP:
      return state.filter(trip => trip.id !== payload)

    case Types.CREATE_BACK_TRIP:
      return [...state, payload]

    case Types.STATUS_TRIP:
      return state.map(el => {
        if (el.id === payload.id) {
          return { ...el, status: payload.status }
        }
        return el
      })

    case Types.CLEAR:
      return []

    default:
      return state
  }
}

const tripReduser = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_TRIP_ID:
      return payload

    case Types.CLEAR:
    case Types.GET_ALL_TRIPS:
      return null

    default:
      return state
  }
}

const periodReduser = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_TRIP_PERIOD:
      return payload

    case Types.CLEAR:
    case Types.GET_ALL_TRIPS:
      return null

    default:
      return state
  }
}

const routeReduser = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_TRIP_ROUTE:
      return payload

    case Types.GET_ROUTE_STOPS:
      return [...state, payload]

    case Types.GET_ALL_TRIPS:
    case Types.CLEAR:
      return []

    default:
      return state
  }
}

const tariffReduser = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_TRIP_TARIFF:
      return payload

    case Types.CHANGE_TRIP_TARIFF:
      return payload

    case Types.GET_ALL_TRIPS:
    case Types.CLEAR:
      return []

    default:
      return state
  }
}

const currentStopReduser = (state = '', { type, payload }) => {
  switch (type) {
    case Types.GET_CURRENT_STOP:
      return payload

    case Types.GET_ALL_TRIPS:
    case Types.CLEAR:
      return ''

    default:
      return state
  }
}
const typeEditReducer = (state = '', { type, payload }) => {
  switch (type) {
    case Types.TYPE_EDIT_TRIP:
      return payload

    case Types.GET_ALL_TRIPS:
      return ''

    default:
      return state
  }
}

const loaderReduser = (state = false, { type }) => {
  switch (type) {
    case Types.START_TRIPS_LOADER:
      return true

    case Types.TRIPS_ERROR:
    case Types.GET_ALL_TRIPS:
    case Types.GET_TRIP_ID:
    case Types.GET_TRIP_PERIOD:
    case Types.GET_TRIP_ROUTE:
    case Types.GET_ROUTE_STOPS:
    case Types.GET_TRIP_TARIFF:
    case Types.CLEAR:
      return false

    default:
      return state
  }
}
const errorReduser = (state = null, { type, payload }) => {
  switch (type) {
    case Types.TRIPS_ERROR:
      return payload

    case Types.GET_ALL_TRIPS:
    case Types.CLEAR:
      return null

    default:
      return state
  }
}

export const trips = combineReducers({
  trips: tripsReduser,
  trip: tripReduser,
  period: periodReduser,
  route: routeReduser,
  tariff: tariffReduser,
  currentStop: currentStopReduser,
  typeTrip: typeEditReducer,
  loader: loaderReduser,
  error: errorReduser,
})
