import React, { Suspense, lazy } from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'

const OrdersPage = lazy(() => import('../pages/OrdersPage'))
const OrdersResultPage = lazy(() => import('../pages/OrdersResultPage'))

const SchedulePage = lazy(() => import('../pages/SchedulePage'))
const TripsPage = lazy(() => import('../pages/TripsPage'))
const VehiclesPage = lazy(() => import('../pages/VehiclesPage'))
const StaffPage = lazy(() => import('../pages/StaffPage'))
const ReportsResultPage = lazy(() => import('../pages/ReportsResultPage'))

const ReportsPage = lazy(() => import('../pages/ReportsPage'))
const CarrierPage = lazy(() => import('../pages/CarrierPage'))
const PassengersPage = lazy(() => import('../pages/PassengersPage'))
const BusesPage = lazy(() => import('../pages/BusesPage'))
const CrewPage = lazy(() => import('../pages/CrewPage'))
const TripMain = lazy(() => import('../pages/TripMainPage'))
const TripEdit = lazy(() => import('../pages/TripEditPage'))
const TripPeriods = lazy(() => import('../pages/TripPeriodsPage'))
const TripRoute = lazy(() => import('../pages/TripRoutePage_D'))
// const TripRoute = lazy(() => import('../pages/TripRoutePage'))
const TripRouteMap = lazy(() => import('../pages/TripRouteMapPage'))
const TripTariff = lazy(() => import('../pages/TripTariffPage'))

const Pages = () => {
  return (
    <Suspense fallback={<p> Loading ...</p>}>
      <Switch>
        <Route path="/schedule" component={SchedulePage} />
        <Route path="/orders/result" component={OrdersResultPage} />
        <Route path="/orders" component={OrdersPage} />
        <Route path="/trips" component={TripsPage} />
        <Route path="/vehicles" component={VehiclesPage} />
        <Route path="/staff" component={StaffPage} />
        <Route path="/reports/result" component={ReportsResultPage} />

        <Route path="/reports" component={ReportsPage} />
        <Route path="/carrier" component={CarrierPage} />
        <Route path="/passengers" component={PassengersPage} />
        <Route path="/buses" component={BusesPage} />
        <Route path="/crew" component={CrewPage} />
        <Route path="/trip-edit" component={TripEdit} />
        <Route path="/trip-main" component={TripMain} />
        <Route path="/trip-periods" component={TripPeriods} />
        <Route path="/trip-route" component={TripRoute} />
        <Route path="/trip-map" component={TripRouteMap} />
        <Route path="/trip-tariff" component={TripTariff} />

        <Redirect to="schedule" />
      </Switch>
    </Suspense>
  )
}

export default Pages
