import { createSelector } from 'reselect'

export const getLang = state => state.global.language

export const getAuth = state => state.global.auth

export const pageTitle = state => state.global.pageTitle

export const getPageTitle = createSelector(pageTitle, name => {
  switch (name) {
    case 'home':
      return 'Главная'
    case 'orders':
      return 'Заказы'
    case 'schedule':
      return 'Расписание'
    case 'trips':
      return 'Рейсы'
    case 'vehicles':
      return 'Автобусы'
    case 'staff':
      return 'Сотрудники'
    case 'reports':
      return 'Отчет по продажам'
    case 'carrier':
      return 'Перевозчик'

    default:
      break
  }
})
