import React from 'react'
import styled from 'styled-components'
import Navigation from './Navigation'
import Pages from './Pages'
import '../stylesheets/animation.css'
import Header from './Header'
import { device } from '../stylesheets/device'
import { useLocation } from 'react-router-dom'

const width = window.innerWidth

const Dashboard = () => {
  const location = useLocation()
  const isHeader = () => {
    if (
      location.pathname === '/passengers' ||
      location.pathname === '/buses' ||
      location.pathname === '/crew' ||
      location.pathname === `/trip-main` ||
      location.pathname === '/trip-edit' ||
      location.pathname === '/trip-periods' ||
      location.pathname === '/trip-route' ||
      location.pathname === '/trip-tariff' ||
      location.pathname === '/trip-map' ||
      location.pathname === '/reports/result' ||
      location.pathname === '/orders/result'
    )
      return false
    return true
  }
  return (
    <>
      {width >= 992 ? (
        <Flex>
          <Navigation />
          <Div>
            {isHeader() && <Header />}
            <Pages />
          </Div>
        </Flex>
      ) : (
        <Div>
          {isHeader() && <Header />}
          <Pages />
        </Div>
      )}
    </>
  )
}

export default Dashboard

const Flex = styled.div`
  display: flex;
`
const Div = styled.div`
  flex: 1;
  @media ${device.laptop} {
  }
`
