import { combineReducers } from 'redux'
import { Types } from '../actionTypes'

const vehiclesReducer = (state = [], { type, payload }) => {
  switch (type) {
    case Types.GET_VEHICLES_ALL:
      return payload
    case Types.EDIT_VEHICLE:
      return [...state.filter(el => el.id !== payload.id), payload]
    case Types.ADD_VEHICLE:
      return [...state, payload]
    case Types.REMOVE_VEHICLE:
      return state.filter(el => el.id !== payload)
    default:
      return state
  }
}

const vehicleReduser = (state = null, { type, payload }) => {
  switch (type) {
    case Types.GET_VEHICLE_ID:
      return payload

    default:
      return state
  }
}

const errorReducer = (state = null, { type, payload }) => {
  switch (type) {
    case Types.VEHICLES_ERROR:
      return payload

    default:
      return state
  }
}

const loaderReducer = (state = false, { type }) => {
  switch (type) {
    case Types.START_VEHICLES_LOADER:
      return (state = true)

    case Types.VEHICLES_ERROR:
    case Types.GET_VEHICLES_ALL:
    case Types.GET_VEHICLE_ID:
    case Types.ADD_VEHICLE:
      return (state = false)

    default:
      return state
  }
}

export const vehicles = combineReducers({
  vehicles: vehiclesReducer,
  vehicle: vehicleReduser,
  error: errorReducer,
  loader: loaderReducer,
})
