// enf
import axios from 'axios'

axios.defaults.baseURL = 'https://car.busis.eu'

// вход в систему
export const authorization = key => {
  return axios.post(
    '/api/user/auth',
    {},
    {
      headers: { Authorization: `Basic ${key}` },
    }
  )
}

// добавить автобус
export const addVehicle = (data, auth) => {
  return axios.post(`/api/vehicle`, data, {
    headers: { Authorization: auth },
  })
}

// редактировать автобус
export const editVehicle = (data, auth) => {
  return axios.put(`/api/vehicle`, data, {
    headers: { Authorization: auth },
  })
}

// удалить автобус
export const removeVehicle = (id, auth) => {
  return axios.delete(`/api/vehicle?vehicleId=${id}`, {
    headers: { Authorization: auth },
  })
}

