// назначить сотрудника на маршрут
export const toAppoint = (memberId, tripVehicleId, auth) => {
  return axios.post(
    `api/schedule/vehicle/member?tripVehicleId=${tripVehicleId}&memberId=${memberId}`,
    {},
    {
      headers: { Authorization: auth },
    }
  )
}

