//  получить отчеты
export const getReports = (from, to, type, auth) => {
  return axios(`/api/report/sales?from=${from}&to=${to}&bySale=${type}&lang=RU`, {
    headers: { Authorization: auth },
  })
}

//  поиск заказа
export const getOrders = (queryStr, auth) => {
  return axios(`/api/report/search?${queryStr}&lang=UA`, {
    headers: { Authorization: auth },
  })
}
