import { useSelector, useDispatch } from 'react-redux'
import Select from 'react-select'
import styled from 'styled-components'
import { setLang } from '../redux/global/globalAction'
import { getLang } from '../redux/global/globalSelectors'

const options = [
  { value: 'UA', label: 'UA' },
  { value: 'RU', label: 'RU' },
  { value: 'EN', label: 'EN' },
]

const LanguageSelect = () => {
  const lang = useSelector(getLang)
  const dispatch = useDispatch()
  const handleChange = option => {
    dispatch(setLang(option))
  }
  return (
    <StyledSelect
      classNamePrefix={'Select'}
      value={lang}
      onChange={handleChange}
      options={options}
    />
  )
}

export default LanguageSelect

const StyledSelect = styled(Select)`
  & .Select__control {
    width: 56px;
    margin: 0 0 0 auto;
    border: none;
    box-shadow: none;
    background-color: transparent;
    cursor: pointer;
    &:hover {
      font-weight: 700;
    }
  }
  & .Select__indicator {
    padding: 5px 0;
  }
  & .Select__indicator,
  .Select__single-value {
    color: ${prop => prop.theme.successColor};
    font-size: 18px;
    &:hover {
      color: ${prop => prop.theme.successColor};
    }
  }
  & .Select__indicator-separator {
    display: none;
  }

  & .Select__menu {
    width: 56px;
    border-radius: 6px;
    margin-top: -6px;
    overflow: hidden;
  }
  & .Select__menu-list {
    padding: 0px;
  }
  & .Select__option {
    color: #000;
    &:hover {
      background-color: #feffcc;
    }
  }
  & .Select__option--is-selected {
    background-color: ${prop => prop.theme.mainColor};
  }
  & .Select__option--is-focused {
    background-color: #feffcc;
  }
`
